.auth-container {
    max-width: 400px;
    margin: 3rem auto;
    padding: 2rem;
    background-color: #f4f4f4;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  
    h2 {
      text-align: center;
      margin-bottom: 1.5rem;
      color: #333;
    }
  
    .auth-form {
      display: flex;
      flex-direction: column;
  
      .form-group {
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
  
        label {
          font-weight: 600;
          margin-bottom: 0.5rem;
        }
  
        input {
          padding: 0.5rem;
          border: 1px solid #ccc;
          border-radius: 4px;
          font-size: 1rem;
        }
      }
  
      button {
        margin-top: 1rem;
        padding: 0.75rem;
        background-color: #4caf50;
        color: white;
        border: none;
        border-radius: 4px;
        font-size: 1rem;
        cursor: pointer;
  
        &:hover {
          background-color: #45a049;
        }
      }
    }
  
    .error {
      margin-top: 1rem;
      color: #c0392b;
      text-align: center;
      font-weight: 500;
    }
  
    .switch {
      text-align: center;
      margin-top: 1rem;
  
      a {
        color: #007bff;
        text-decoration: none;
  
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  